/**
 * @name FormCreateEvent
 * @version 0.0.1
 */

/* --- Global --- */
import {useState} from 'react';
import useForm from 'react-hook-form';
import {ethers, utils} from 'ethers';

/* --- Local --- */
import Deployer from 'contracts/Deployer';

/* --- Component --- */
const FormCreateEvent = props => {
  /* --- Form Hook State --- */
  const {handleSubmit, register, errors} = useForm();

  /* --- Component State --- */
  const [isComplete, setComplete] = useState();

  /* --- Submit Handler --- */
  const onSubmit = async values => {
    if (values) {
      setComplete(true);

      // The Contract interface
      let abi = Deployer.abi;
      let provider = await ethers.getDefaultProvider('goerli');
      provider = await new ethers.providers.Web3Provider(
        window.web3.currentProvider,
      );
      const wallet = provider.getSigner();
      let contractAddress = process.env.REACT_APP_EVENT_CONTRACT;
      let contract = await new ethers.Contract(contractAddress, abi, wallet);

      const amount = utils.hexlify(utils.parseEther(values.deposit));
      const limitOfParticipants = utils.hexlify(
        Number(values.limitOfParticipants),
      );
      const coolingPeriod = utils.hexlify(
        Number(utils.bigNumberify(values.coolingPeriod)),
      );

      const token = values.token;

      const tx = await contract.deploy(
        values.title,
        amount,
        limitOfParticipants,
        coolingPeriod,
        token,
      );
    }
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Atom.Flex column sx={{flex: 1}}>
        <Molecule.Field
          name="title"
          variants={['text']}
          label="Name"
          placeholder="The event name."
          disabled={isComplete}
          register={register}
          errors={errors}
        />
        <Molecule.Field
          name="deposit"
          label="Deposit"
          placeholder="The deposit (ETH or ERC20) required to purchase ticket."
          variants={['text']}
          disabled={isComplete}
          register={register}
          errors={errors}
        />
        <Molecule.Field
          name="limitOfParticipants"
          type="number"
          label="Limit of Participants"
          placeholder="Example: 20"
          variants={['text']}
          disabled={isComplete}
          register={register}
          errors={errors}
        />
        <Molecule.Field
          name="coolingPeriod"
          defaultValue="604800"
          type="number"
          label="Cooling Period"
          placeholder="The amount of time required for registrants can withdraw deposit."
          variants={['text']}
          disabled={isComplete}
          register={register}
          errors={errors}
        />
        <Molecule.Field
          name="token"
          label="ERC20 Token (Default ETH payment)"
          defaultValue="0x0000000000000000000000000000000000000000"
          variants={['text']}
          disabled={isComplete}
          register={register}
          errors={errors}
        />

        <Atom.Box sx={{mt: 4}}>
          <Atom.Button bg="blue" color="white" sx={{m: 0}}>
            {isComplete ? (
              <Atom.Span>Creating...</Atom.Span>
            ) : (
              <Atom.Span>Create Event</Atom.Span>
            )}
          </Atom.Button>
        </Atom.Box>
      </Atom.Flex>
    </form>
  );
};

export default FormCreateEvent;
